*, ::after, ::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box; }

html {
    font-size: 62.5%; }

body {
    font-family: 'Roboto';
    font-weight: 400;
    color: #777;
    font-size: 1.6rem;
    letter-spacing: .2rem;
    padding: 5rem;
    background-color: #FFFFFF; }

.container {
    background-color: #F7F7F7; }

.header {
    position: relative;
    height: 95vh;
    background: linear-gradient(90deg, rgba(186, 133, 84, 0.70) 0.03%, rgba(1, 1, 1, 0.70) 99.94%), url(../img/header-bg.jpg) center/cover no-repeat;
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 80%);
    &__logo {
        position: absolute;
        left: 5rem;
        top: 5rem;
        height: 7rem;
        width: 5rem; }
    &__box {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        text-align: center; } }


.title-first {
    color: #FFF;
    &__main {
        display: block;
        font-size: 5rem;
        font-weight: 800;
        line-height: 6rem;
        letter-spacing: 1rem;
        text-transform: uppercase;
        margin-bottom: 1rem;
        animation-name: leftToRight;
        animation-duration: 1.7s;
        animation-delay: 1s;
        animation-timing-function: ease;
        backface-visibility: hidden; }
    &__secondory {
        display: block;
        font-size: 4rem;
        font-weight: 100;
        line-height: 4.8rem;
        letter-spacing: .5rem;
        animation-name: rightToLeft;
        animation-duration: 1.7s;
        animation-delay: 1s;
        animation-timing-function: ease;
        backface-visibility: hidden;
        margin-bottom: 6.5rem; } }

.btn {
    &:link, &:visited {
        text-transform: uppercase;
        text-decoration: none;
        color: #777;
        background: #fff;
        padding: 1.5rem 4rem;
        border-radius: 5rem;
        display: inline-block;
        transition: all .3s;
        position: relative;
        font-size: 2rem; }
    &:hover {
        transform: translateY(-3px);
        box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.30);
        &::after {
            opacity: 0;
            transform: scaleX(1.3) scaleY(1.5); } }
    &:active {
        transform: translateY(-1px);
        box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.10); }
    &::after {
        content: "";
        background: #fff;
        width: 100%;
        height: 100%;
        border-radius: 5rem;
        display: inline-block;
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        transition: all .4s; }
    &_animated {
        animation: btnToUp .5s ease-out .7s;
        animation-fill-mode: backwards; } }



@keyframes leftToRight {
    0% {
        opacity: 0;
        transform: translate(-10rem); }
    70% {
        transform: translate(1rem); }

    100% {
        opacity: 1;
        transform: translate(0px); } }

@keyframes rightToLeft {
    0% {
        opacity: 0;
        transform: translate(10rem); }
    80% {
        transform: translate(-1rem); }
    100% {
        opacity: 1;
        transform: translate(0px); } }

@keyframes btnToUp {
    0% {
        opacity: 0;
        transform: translateY(5rem); }
    100% {
        opacity: 1;
        transform: translateY(0px); } }
